<template>
  <div class="guid">
    <p class="tip"> <a href="https://github.com/kamranahmedse/driver.js">driver.js</a></p>
    <el-button type="primary" @click="handleStart"></el-button>
  </div>
</template>

<script lang="ts">
import { nextTick, onMounted } from '@vue/runtime-core'
import Driver from 'driver.js'
import 'driver.js/dist/driver.min.css'

export default {
  setup() {
    const driver = new Driver({
      doneBtnText: '',        // Text on the last button
      closeBtnText: '',      // Text on the close button
      nextBtnText: '',        // Next button text
      prevBtnText: ''
    })

    const steps = [
      {
        element: '.navbar-icon',
        popover: {
          title: '',
          description: '/',
          position: 'bottom'
        }
      },
      {
        element: '#navbar-title',
        popover: {
          title: '',
          description: '',
          position: 'bottom'
        }
      },
      {
        element: '#navbar-user-info',
        popover: {
          title: '',
          description: '',
          position: 'left'
        }
      },
      {
        element: '#navbar-github',
        popover: {
          title: 'Github',
          description: 'Github',
          position: 'left'
        }
      }
    ]

    const handleStart = () => {
      setTimeout(() => {
        driver.defineSteps(steps)
        driver.start()
      })
    }

    return {
      handleStart
    }
  }
}
</script>

<style lang="scss" scoped>
.guid {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  padding: 30px;
}
.tip {
  padding: 10px;
  background: skyblue;
  color: #fff;
  font-size: 14px;
  margin-bottom: 15px;
  a {
    color: green;
  }
}
</style>