
import { nextTick, onMounted } from '@vue/runtime-core'
import Driver from 'driver.js'
import 'driver.js/dist/driver.min.css'

export default {
  setup() {
    const driver = new Driver({
      doneBtnText: '',        // Text on the last button
      closeBtnText: '',      // Text on the close button
      nextBtnText: '',        // Next button text
      prevBtnText: ''
    })

    const steps = [
      {
        element: '.navbar-icon',
        popover: {
          title: '',
          description: '/',
          position: 'bottom'
        }
      },
      {
        element: '#navbar-title',
        popover: {
          title: '',
          description: '',
          position: 'bottom'
        }
      },
      {
        element: '#navbar-user-info',
        popover: {
          title: '',
          description: '',
          position: 'left'
        }
      },
      {
        element: '#navbar-github',
        popover: {
          title: 'Github',
          description: 'Github',
          position: 'left'
        }
      }
    ]

    const handleStart = () => {
      setTimeout(() => {
        driver.defineSteps(steps)
        driver.start()
      })
    }

    return {
      handleStart
    }
  }
}
